:root{
	--mainGreen :#027782;
	--mainYellow :#ffcb08;
	--mainDark: #242325;
  	--mainWhite: #fff;
  	--mainGrey: #eeeeee;
}
html{
	scroll-behavior: smooth;

}
body{
	margin: 0;
	padding: 0;
	font-family: 'Open Sans', sans-serif;
	overflow-x: hidden;

}
/*UNIVERSAL STYLES */
.btn-green{
	border: 0.2rem solid var(--mainGreen);
	color: var(--mainGreen);
	font-weight: bold;
}
.btn-green:hover{
	background-color: var(--mainGreen);
	color: var(--mainYellow);
	transition: all 0.2s ease-in-out;
}

.gototopbtn{
	position: fixed;
	right: 30px;
	bottom: 30px;
	width: 50px;
	height:50px;
	line-height: 50px;
	background-color: var(--mainGreen);
	color: var(--mainYellow);
	font-size: 1.5rem;
	text-align: center;
	border-radius: 50%;
	transition: all 0.5s ease;

}
.gototopbtn:hover{
	background-color: var(--mainYellow);
	color: var(--mainGreen);
}
/*NAVBAR */
.navbar-brand{
	width: 15%;
	text-align: center;
}
.navbar-toggler{
	outline: none;
	border:none;
}
.fa-bars{
	font-size: 2rem;
	color: var(--mainGreen);
}
.navbar-toggler:focus{
	outline: none;
	border:none;
}
.nav-item{
	padding-right: 40px;
}
.nav-link{
	font-weight: bold;
	color: black !important;
}
.nav-active{
	color:var(--mainGreen) !important;
}
/*main header */
.height-max{
	min-height: calc(100vh - 100px);
}
#main-head{
	background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url('https://demo2.wpopal.com/pharmacy/wp-content/uploads/revslider/pharmacy_slider/bg-slider2.jpg');
	background-repeat: no-repeat;
	background-position: bottom;
	background-size: cover;
	background-attachment: fixed;
}

/* TITLE */
.title-underline{
	width: 12rem;
	height: 0.3rem;
	border-radius:2rem;
	background-color: var(--mainYellow)!important;
}
.title-head{
	color: var(--mainBlack);
}

/* ABOUT */
.text-green{
	color: var(--mainGreen);
}
.blockquote{
	border-left: 0.3rem solid var(--mainYellow);
}


.title-main{
	font-size: 4rem;
	color: var(--mainGreen);
}
.img-container{
	position: relative;
}
.img-container::before{
	content: "";
	position: absolute;
	top: -1.4rem;
	left: 1.6rem;
	width: 100%;
	height: 100%;
	outline: 0.5rem solid var(--mainGreen);
	z-index: -1;
	transition: all 0.7s linear;
}
.img-container:hover:before{
	top: 0;
	left: 0;
}

/*OUR SERVICES */
.our-services{
	background-image:linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url('https://demo2.wpopal.com/pharmacy/wp-content/uploads/revslider/pharmacy_slider/slide1.png');
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	background-attachment: fixed;
}
.service-text{
	font-size: 3rem;
	color: var(--mainYellow);
}
.service-icon{
	display: inline-block;
	width: 50px;
	height:50px;
	border:0.1rem solid var(--mainYellow);
	color: var(--mainYellow);
	transform: rotate(45deg);
	margin-bottom: 1.5rem;
	margin-top: 1.5rem;
	transition: all 0.5s ease-in-out;

}
.service-icon i{
	line-height: 50px;
	transform: rotate(-45deg);
	font-size: 1.5rem;
}
.service-icon:hover{
	background-color: var(--mainYellow);
	color: var(--mainWhite);
}
h3{
	color: var(--mainYellow);
}

.single-item{
	transition: all 0.3s linear;
	box-shadow: 0 3px 6px rgba(0, 0, 0, 0.3);
}

.img-conatiner-main{
	overflow: hidden;
}
.product-img{
	transition: all 2s ease-in-out;
}
.img-conatiner-main:hover .product-img{
	transform: scale(1.2);
}

.footer{
	background-color: var(--mainWhite);
}
.text{
	color: var(--mainGreen);
}
.foot-text{
	color: var(--mainGreen);
}

#message{
	background-color: #f0f0f0;
}
.form-control{
	outline: none!important;
	border:0.1rem solid transparent!important;
	color: var(--mainBlack) !important;
}
.form-control:focus{
	outline: none!important;
	box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.2);
	transition: all 0.3s ease;
}
.btn-msg{
	background-color: var(--mainGreen);
	color: white;
}